/* Global Styles */
body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7fa;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  min-height: 100vh;
  color: #333;
}

/* Wrapper to centralize the content */
.container {
  width: 100%;
  max-width: 1000px;
  padding: 20px;
  box-sizing: border-box;
  margin: 0 auto;
  text-align: left;
}

/* Header Styling */
h2 {
  font-size: 28px;
  font-weight: 600;
  color: #2d3436;
  margin-bottom: 20px;
  text-align: center;
}

/* Role/Users List Container */
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* List Item Styling */
li {
  background-color: #fff;
  border-radius: 8px;
  margin: 12px 0;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

li:hover {
  background-color: #f9f9f9;
  transform: translateY(-2px);
}

/* Links Styling - Remove underline and style */
li a {
  font-size: 18px;
  color: #3498db;
  text-decoration: none; /* No underline */
  padding: 8px 15px;
  border-radius: 5px;
  background-color: #ecf0f1;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

li a:hover {
  background-color: #3498db;
  color: white;
  transform: translateY(-2px);
}

li a:active {
  transform: translateY(0);
}

/* Buttons Styling */
button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  outline: none;
}

button:hover {
  background-color: #2980b9;
  transform: translateY(-1px);
}

button:active {
  transform: translateY(0);
}

button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

/* Edit & Delete Buttons */
button.edit {
  background-color: #f39c12;
}

button.edit:hover {
  background-color: #e67e22;
}

button.delete {
  background-color: #e74c3c;
}

button.delete:hover {
  background-color: #c0392b;
}

/* Error Message Styling */
.error {
  color: #e74c3c;
  font-weight: bold;
  margin-bottom: 15px;
  padding: 12px;
  border-radius: 5px;
  background-color: #f8d7da;
  border: 1px solid #e74c3c;
}

/* Edit Form Styling */
form {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

form div {
  margin-bottom: 15px;
}

form input {
  padding: 12px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-top: 5px;
  width: 100%;
  box-sizing: border-box;
}

form input:focus {
  border-color: #3498db;
  outline: none;
}

/* Form Button Styling */
form button[type="submit"] {
  background-color: #27ae60;
  font-size: 16px;
  font-weight: 600;
}

form button[type="submit"]:hover {
  background-color: #2ecc71;
}

/* General Layout */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

button[type="button"].cancel {
  background-color: #95a5a6;
  color: #fff;
  font-weight: bold;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

button[type="button"].cancel:hover {
  background-color: #7f8c8d;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  body {
    padding: 20px;
  }

  .container {
    padding: 15px;
    width: 100%;
  }

  h2 {
    font-size: 24px;
  }

  li {
    padding: 12px;
    font-size: 14px;
  }

  form input {
    font-size: 14px;
  }

  button {
    padding: 8px 12px;
    font-size: 12px;
  }
}
